<template>
  <div>
      <div class="bg-white d-flex justify-content-between align-items-center p-1" v-if="data.user">
        <div class="bd-highlight">
          <span @click="$tourl('../')" class="fs-5 bi bi-chevron-left"></span>
        </div>
        <div class="bd-highlight">
            <span class="fs-6">{{data.user.nickname}}</span>
        </div>
        <div class="bd-highlight">
        </div>
      </div>
      <div class="d-flex justify-content-between align-items-center bg-info bg-gradient text-white fs-normal" v-if="data.trip">
        <div class="bd-highlight p-2">
          <div class="fs-normal">{{data.trip.setdate|setdate(data.trip.setdate_time)}} | 待接单</div>
          <div>
            <span>{{data.trip.origin}}</span>
            <span>到</span>
            <span>{{data.trip.destination}}</span>
          </div>
        </div>
        <div class="bd-highlight p-2">
          <span class="d-inline-block text-center border rounded-pill bg-white fs-6 text-black-50 mx-1 bi bi-telephone-fill" style="width: 30px; height: 30px;"></span>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  components: { 
  },
  props: {
    set:{},
    data:{},
  },
  methods:{
      
  }
}
</script>


